import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader";
import { TrackballControls } from "three/examples/jsm/controls/TrackballControls";
import { DiscreteInterpolant, Shape } from "three";
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / innerHeight,
  0.1,
  4000
);

camera.position.z = 8;
const container = document.getElementById("c");

const renderer = new THREE.WebGLRenderer({ antialias: true });
document.body.appendChild(container);

renderer.setClearColor("#233143");
renderer.setSize(container.offsetWidth, container.offsetHeight);
document.body.appendChild(container);
container.appendChild(renderer.domElement);

window.addEventListener("resize", () => {
  renderer.setSize(container.offsetWidth, container.offsetHeight);
  camera.aspect = window.innerWidth / innerHeight;
  camera.updateProjectionMatrix();
});

const boxGeometry = new THREE.BoxGeometry(2, 2, 2);
const boxMaterial = new THREE.MeshLambertMaterial({ color: 0xffffff });
const boxMesh = new THREE.Mesh(boxGeometry, boxMaterial);
boxMesh.name = "cube"

const pyramidGeometry = new THREE.CylinderGeometry(0, 3, 3, 4, 1);
const pyramidMaterial = new THREE.MeshLambertMaterial({ color: 0xffffff });
const pyramidMesh = new THREE.Mesh(pyramidGeometry, pyramidMaterial);
pyramidMesh.name = 'pyramid'

const cylinderGeometry = new THREE.CylinderGeometry(2, 2, 3, 100);
const cylinderMaterial = new THREE.MeshLambertMaterial({ color: 0xffffff });
const cylinderMesh = new THREE.Mesh(cylinderGeometry, cylinderMaterial);
cylinderMesh.name = 'cylinder'

const torusGeometry = new THREE.TorusGeometry(0.8, 0.5, 100, 50);
const torusMaterial = new THREE.MeshLambertMaterial({ color: 0xffffff });
const torusMesh = new THREE.Mesh(torusGeometry, torusMaterial);
torusMesh.name = 'torus'

const lights = [];
// const lightHelpers = [];
const lightValues = [
  { colour: 0x14d14a, intensity: 8, dist: 12, x: 1, y: 0, z: 8 },
  { colour: 0xbe61cf, intensity: 6, dist: 12, x: -2, y: 1, z: -10 },
  { colour: 0x00ffff, intensity: 3, dist: 10, x: 0, y: 10, z: 1 },
  { colour: 0x00ff00, intensity: 6, dist: 12, x: 0, y: -10, z: -1 },
  { colour: 0x16a7f5, intensity: 6, dist: 12, x: 10, y: 3, z: 0 },
  { colour: 0x90f615, intensity: 6, dist: 12, x: -10, y: -1, z: 0 },
];

// const lightValues = [
//   { colour: 0xffffff, intensity: 8, dist: 12, x: 1, y: 0, z: 8 },
//   { colour: 0xffffff, intensity: 6, dist: 12, x: -2, y: 1, z: -10 },
//   { colour: 0xffffff, intensity: 3, dist: 10, x: 0, y: 10, z: 1 },
//   { colour: 0xffffff, intensity: 6, dist: 12, x: 0, y: -10, z: -1 },
//   { colour: 0xffffff, intensity: 6, dist: 12, x: 10, y: 3, z: 0 },
//   { colour: 0xffffff, intensity: 6, dist: 12, x: -10, y: -1, z: 0 },
// ];

for (let i = 0; i < 6; i++) {
  lights[i] = new THREE.PointLight(
    lightValues[i]["colour"],
    lightValues[i]["intensity"],
    lightValues[i]["dist"]
  );
  lights[i].position.set(
    lightValues[i]["x"],
    lightValues[i]["y"],
    lightValues[i]["z"]
  );
  scene.add(lights[i]);
  // lightHelpers[i] = new THREE.PointLightHelper(lights[i], 0.7);
  // scene.add(lightHelpers[i]);
}

// Axes Helper
// const axesHelper = new THREE.AxesHelper(5);
// scene.add(axesHelper); // X == red, Y == green, Z == blue

const sphereMeshes = [];
const sphereGeometry = new THREE.SphereGeometry(0.1, 32, 32);
const sphereMaterial = new THREE.MeshLambertMaterial({ color: 0xc56cef });

for (let i = 0; i < 4; i++) {
  sphereMeshes[i] = new THREE.Mesh(sphereGeometry, sphereMaterial);
  sphereMeshes[i].position.set(0, 0, 0);
  scene.add(sphereMeshes[i]);
}

let theta = 0;
const dtheta = (2 * Math.PI) / 100;

const controls = new TrackballControls(camera, renderer.domElement);
controls.rotateSpeed = 4;
controls.dynamicDampingFactor = 0.15; //WORKS LIKE FRICTION

function getShape() {
  let value = document.getElementById("shapes").value;

  switch (value) {
    case "cube":
      scene.add(boxMesh);
      scene.remove(pyramidMesh);
      scene.remove(torusMesh);
      scene.remove(cylinderMesh);
      break;
    case "pyramid":
      scene.add(pyramidMesh);
      scene.remove(torusMesh);
      scene.remove(cylinderMesh);
      scene.remove(boxMesh);
      break;
    case "donut":
      scene.add(torusMesh);
      scene.remove(cylinderMesh);
      scene.remove(boxMesh);
      scene.remove(pyramidMesh);
      break;
    case "cylinder":
      scene.add(cylinderMesh);
      scene.remove(boxMesh);
      scene.remove(pyramidMesh);
      scene.remove(torusMesh);
  }
}
  
function setRotation() {
  let y_rotation = document.getElementById("yRot").value;
  let x_rotation = document.getElementById("xRot").value;
  let z_rotation = document.getElementById("zRot").value;

  console.log(y_rotation, x_rotation, z_rotation);
  scene.rotation.y -= y_rotation;
  scene.rotation.x -= x_rotation;
  scene.rotation.z -= z_rotation;
}


function setColor(){
  let objects = scene.children;
  let obj = (objects.slice(-1));
  let shape = scene.getObjectByName(obj[0]['name']);
  console.log(shape)
  // console.log( shape['material']['color'])
  //  shape['material']['color']['r'] = 400;
  //  shape['material']['color']['g'] = 535;
  //  shape['material']['color']['b'] = 535;


}


function animate() {
  requestAnimationFrame(animate);
  setRotation();
  getShape();
  setColor();
  theta += dtheta;
  const trigs = [
    {
      x: Math.cos(theta * 0.5),
      y: Math.sin(theta * 0.05),
      z: Math.cos(theta * 0.5),
      r: 2,
    },
    {
      x: Math.cos(theta * 0.3),
      y: Math.sin(theta * 0.3),
      z: Math.sin(theta * 0.3),
      r: 2.25,
    },
    {
      x: Math.cos(theta * 0.25),
      y: Math.cos(theta * 0.25),
      z: Math.sin(theta * 0.25),
      r: 2.5,
    },
    {
      x: Math.sin(theta * 0.3),
      y: Math.cos(theta * 0.3),
      z: Math.sin(theta * 0),
      r: 2.75,
    },
  ];
  for (let i = 0; i < 4; i++) {
    sphereMeshes[i].position.x = trigs[i]["r"] * trigs[i]["x"];
    sphereMeshes[i].position.y = trigs[i]["r"] * trigs[i]["y"];
    sphereMeshes[i].position.z = trigs[i]["r"] * trigs[i]["z"];
  }
  controls.update();
  renderer.render(scene, camera);
}

animate();
